let DataFrom = {}
export default {
    data() {
        return {
            list: [{
                title: '',
                sales: 0
            }]
        }
    },
    onLoad(option) {
      
        DataFrom = this.Cacher.getData(option.from);  
        this.Request('getMultiGoodSale', {
            goods_id: DataFrom.detail.info4.sale.goodsId,
        }).then(res => {
            if (res.error == 0) {
                this.list = res.option.sort((a,b)=>b.sales-a.sales); 
            }
        })
    }
}
